.albumPhoto {
  max-width: 44px;
  max-height: 44px;
}

.bx--search {
  margin: 0 !important;
  width: 100% !important;
}

.bx--data-table-container {
  padding: 0;
  /* header 48px, search bar 40px */
  height: calc(100vh - 48px - 40px);
  height: calc(1vh * 100 - 48px - 40px);
  height: calc(var(--vh, 1vh) * 100 - 48px - 40px);
  overflow: scroll;
}

@media (min-width: 66rem) {
  .bx--col-lg-10 {
    max-width: 82.6%;
  }
}

img {
  object-fit: cover;
  max-width: 36px;
  max-height: 36px;
  margin: 6px;
}

pre {
  color: red;
}

#Queue {
  display: flex;
  flex-flow: column nowrap;
}

.queue-track {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 1.6rem;
  height: 3rem;
}

.queue-track > div:last-child {
  margin-left: auto;
}

.queue-track > div {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.queue-track-vote-col > div > svg {
  cursor: pointer;
}

.queue-track-vote-col > div:nth-child(1) {
  margin-bottom: -1rem;
}

.queue-track-vote-col > div:nth-child(1) > svg[class*="active"] {
  fill: #24a148;
}

@media only screen and (max-width: 672px) {
  .bx--grid {
    margin-top: 0px !important;
  }
  #MobileQueueWrapper {
    /* background-color: red !important; */
  }
}

html,
body {
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
}

.bx--grid {
  max-width: none;
  padding: 0;
  padding-left: calc(1rem - 1px);
  margin-top: -1px;
}

.bx--row > div {
  padding: 0 !important;
}

.bx--header {
  position: static;
}
.bx--header__nav {
  display: block;
}
.bx--search-magnifier {
  z-index: 0;
}

#DesktopQueueWrapper,
#MobileQueueWrapper {
  height: calc(100vh - 48px);
  height: calc(1vh * 100 - 48px);
  height: calc(var(--vh, 1vh) * 100 - 48px);
  background-color: #262626;
  color: #f4f4f4;
  padding-right: 0.4rem;
}

@media only screen and (max-width: 1056px) {
  .bx--grid {
    padding-right: calc(1rem - 1px);
  }
}

